<template>
  <v-col xs="12" sm="6" md="4" lg="3" xl="3">
    <v-hover>
      <template v-slot:default="{ hover }">
        <v-card class="mx-auto" max-width="344">
          <v-row v-show="hover">
            <v-col>
              <edit-dialog v-on:getVal="getVal" :sectionData="dataModel" />
              <delete-dialog v-on:getVal="getVal" :sectionData="dataModel" />
            </v-col>
          </v-row>
          <v-img
            :lazy-src="minUrlImage + dataModel.image_url"
            :src="minUrlImage + dataModel.image_url"
            height="200px"
          ></v-img>

          <v-card-title v-text="dataModel.name[lang]"> </v-card-title>
          <v-col cols="12">
            <v-chip
              outlined
              v-if="dataModel.nation"
              v-text="dataModel.nation.name[lang]"
              class="primary"
            >
              Default
            </v-chip>
          </v-col>
          <v-card-subtitle v-if="dataModel.areas">
            areas :
            <v-chip-group active-class="primary--text" column>
              <v-chip v-for="area in areas" :key="area.id">
                {{ area.name[lang] }}
              </v-chip>
            </v-chip-group>
          </v-card-subtitle>

          <v-card-actions>
            <v-btn color="orange lighten-2" text>Description</v-btn>

            <v-spacer></v-spacer>
            <v-btn icon @click="show = !show">
              <v-icon>{{
                show ? "mdi-chevron-up" : "mdi-chevron-down"
              }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="show">
              <v-divider></v-divider>

              <v-card-text v-text="dataModel.description[lang]"> </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </template>
    </v-hover>
  </v-col>
</template>

<script>
export default {
  props: ["dataSection"],
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      tags: [
        "Work",
        "Home Improvement",
        "Vacation",
        "Food",
        "Drawers",
        "Shopping",
        "Art",
        "Tech",
        "Creative Writing",
      ],
      show: false,
      minUrlImage: this.$store.state.imageUlr + "section/",
      dialogEdit: false,
      dialogDelete: false,
      dataModel: this.dataSection,
      areas: this.dataSection.areas,
    };
  },
  methods: {
    getVal() {
      console.log("get vvval in card section ");
      this.$emit("getVal");
    },
  },
  components: {
    "edit-dialog": () => import("../section/edit.vue"),
    "delete-dialog": () => import("../section/delete.vue"),
  },
};
</script> 

<style>
#child {
  position: absolute;
  z-index: 1023;
  right: 10px;
  bottom: 10px;
}
#card {
  justify-content: center;
  text-align: center;
}
#dialog {
  z-index: 1023;
}
</style>





